
















import {Component, Vue} from 'vue-property-decorator';
import {Button} from 'vant';
import CertShow from '@/componets/cert_show.vue';
import {CertOwn, Status} from '@/entry/diamond';

import 'vant/lib/row/style';
import 'vant/lib/col/style';
import 'vant/lib/button/style';
import {notes, NoteType} from '@/note';

Vue.use(Button);

@Component({
  components: {CertShow},
})
export default class Confirm extends Vue {
    private certOwn!: CertOwn;
    public created(): void {
        this.certOwn = this.$store.state.data.certOwn;
        this.$store.state.title = this.$t('cert.confirm');
    }
    public onClickDConfirm(): void {
        this.$store.state.data.diamond.chainCert(this.certOwn.cert).then((it: Status) => {
          if (it === Status.Success) {
              notes.add({type: NoteType.Info, message: this.$t('notifyMsg.success').toString()});
              this.$router.back();
              this.$router.replace('/frame/cert/process');
          } else if (it === Status.Cancel) {
              notes.add({type: NoteType.Info, message: this.$t('notifyMsg.cancel').toString()});
          } else if (it === Status.ErrParameters) {
              notes.add({type: NoteType.Error, message: this.$t('notifyMsg.parError').toString()});
          }
        }).catch((e: any) => {
           notes.add({type: NoteType.Error, message: e.toString()});
        });
    }
}
